<template>
    <div :style="'width:'+ width + 'px'">
        <Row :gutter="10" v-if=" showToolbar !== false ">
            <Col  :xs="24" :sm="9" :md="9" :lg="9"   class="btn-group">
                <slot name="headerAction"></slot>
                <Button v-if="showAddBtn" @click="addForm"  type="primary"><Icon type="md-add-circle" /> </Button>

            </Col>
            <Col :xs="24" :sm="15" :md="15" :lg="15"  class="table-tools">
                <Row type="flex" justify="end">
                    <div class="search-input">
                        <Input
                                placeholder=""
                                v-model="keywords"
                                @on-change="search"
                                clearable>
                        </Input>
                    </div>

                    <Button type="default"  title="" @click="refresh"><Icon type="md-refresh" /></Button>
                    <Button type="default"  title="" @click="filter_show = true"><Icon type="ios-funnel" /></Button>

                    <!--<el-button type="default" size="small" title=""><i class="fa fa-calendar"></i></el-button>
                    <el-button type="default" size="small" title="" @click="pivot"><i class="fa fa-table"></i></el-button>
                    <el-button type="default" size="small" title=""><i class="fa fa-bar-chart"></i></el-button>
                    <el-button type="default" size="small" title=""><i class="fa fa-th-large"></i></el-button>-->

                    <Poptip placement="bottom-end" :width="200" >
                        <Button type="default"  title="">
                            <Icon type="ios-apps" />
                        </Button>
                        <div slot="content">
                            <CheckboxGroup v-model="checkList" class="dropdown-content" @on-change="fieldChange">
                                <template v-for="(item,index) in fields_data">
                                    <Checkbox  :label="item.slot" >
                                        <span v-html="item.title"></span>
                                    </Checkbox><br>
                                </template>
                            </CheckboxGroup>
                        </div>
                    </Poptip>

                    <Button type="default"  title="" @click="importModal = true"><Icon type="md-cloud-upload" /></Button>

                    <Dropdown trigger="click" @on-click="downloadExport" :transfer="true"  placement="bottom-end">
                        <Button type="default"  title="">
                            <Icon type="md-download" />
                        </Button>
                        <DropdownMenu slot="list">
                            <DropdownItem name="csv">CSV</DropdownItem>
                            <DropdownItem name="xlsx">MS-Excel</DropdownItem>
                            <DropdownItem name="xml">XML</DropdownItem>
                        </DropdownMenu>
                    </Dropdown>


                </Row>
            </Col>
        </Row>

        <Table
                :data="tableData"
                :columns="columns"
                border
                @on-sort-change="sort"
                :loading="loading"
                :stripe="true"
                :height="height"
                :width="width"
                @on-select="currentSelect"
                @on-selection-change="getSelectRows"
                @on-select-all="selectAll"
                @on-select-all-cancel="selectAllCancel"
        >
            <!--  -->
            <template v-for="(item,index) in columns"  slot-scope="{ row }"   :slot="item.slot"  >
                <span  v-html="formatter(row,item.slot,item.data_type,item.options,item.options_name)"></span>
            </template>

            <!--  -->
            <template slot-scope="{ row, index }" slot="action" >
                <template v-if=" typeof row['status'] != 'undefined' && row['status'] == 2">
                    <span style="color:#ed4014"></span>
                </template>
                <template v-else>
                    <template v-if="showEditBtn">
                        <Button style="margin-right: 5px"
                                type="success"
                                size="small"
                                @click.native.prevent="editForm(index, row)" ><Icon type="ios-create" /> </Button>
                    </template>
                    <template v-if="showDelBtn">
                        <Button style="margin-right: 5px"
                                type="error"
                                size="small"
                                @click.native.prevent="delForm(row)" ><Icon type="md-trash" /> </Button>
                    </template>

                    <!--  -->
                    <slot name="rowAction" :row="row"></slot>
                </template>
                
            </template>

            <template slot="loading">
                <Icon type="ios-loading" size=18 class="demo-spin-icon-load"></Icon>
                <div>Loading</div>
            </template>

        </Table>

        <div class="pagination">
            <Page
                    @on-page-size-change="handleSizeChange"
                    @on-change="handleCurrentChange"
                    :current="currentPage"
                    :page-size="pageSize"
                    :total="total"
                    :page-size-opts="[5, 20, 30, 40,50,100,200,300,500,1000]"
                    :show-total="showTotal"
                    :show-elevator="showElevator"
                    :show-sizer="showSizer"
                    >
            </Page>
        </div>

        <!--  -->
        <Modal
                title="..."
                v-model="downloadTips"
                width="30%"
                :closable="false"
                >
            <span class="ivu-tag-red">{{downloadError}}</span>
            <Progress  :stroke-width="18" :percent="percentage"></Progress>
            <div slot="footer"></div>
        </Modal>

        <!--  -->
        <vc-form :token="token" :data-form-title="dataFormTitle"  :editor-config="editorConfig"   :model-width="modelWidth" :model-height="modelHeight"  :form-label-width="formLabelWidth" :data-form="dataForm" :rule-validate="ruleValidate" :upload-file-server="uploadFileServer" :upload-file-max-size="uploadFileMaxSize"  :form-tabs="formTabs"  :fields-data="fields_data" ref-name="addDataForm" ref="addDataDlg"  @on-save-data-form="saveAddDataForm"  @on-close-dialog="refresh"></vc-form>

        <!--  -->
        <vc-form :token="token" :data-form-title="dataFormTitle"  :editor-config="editorConfig"  :model-width="modelWidth"  :model-height="modelHeight"  :form-label-width="formLabelWidth" :data-form="editDataForm" :rule-validate="ruleValidate" :upload-file-server="uploadFileServer" :upload-file-max-size="uploadFileMaxSize"  :form-tabs="formTabs"  :fields-data="fields_data" ref-name="editDataForm" ref="editDataDlg"  @on-save-data-form="saveEditDataForm" @on-close-dialog="refresh"></vc-form>


        <!--  -->
        <Modal v-model="importModal" title="" :closable="false">
            <Row class="import-btn">
                <Col :xs="24" :sm="12" :md="12" :lg="12" :xl="12">
                    <Button icon="md-cloud-download" type="success" @click="downloadTemplate" ></Button>
                </Col>
                <Col :xs="24" :sm="12" :md="12" :lg="12" :xl="12">
                    <input type="file" ref="importExcelForm" class="file-form" @change="importExcel" >
                    <Button icon="md-cloud-upload" type="primary" @click="triggerUpload"></Button>
                </Col>
                <Col :xs="24" :sm="24" :md="24" :lg="24" :xl="24"  v-if=" import_file_name != '' ">
                    :  {{ import_file_name }} 
                    <div v-if=" import_file_error != '' ">
                        <div class="error_tips" v-html="import_file_error"></div>
                    </div>
                </Col>

            </Row>
            
            <Row>
                <Col>
                    <Progress  :stroke-width="18" :percent="importExcelPercentage" text-inside></Progress>
                </Col>
            </Row>

            <template slot="footer">
                <Button type="default"   @click="closeImportDlg"></Button>
                <Button type="primary"   @click="startImportData" :disabled=" is_import_disabled "></Button>
            </template>
        </Modal>

        <!--  -->
        <Modal :width="660" v-model="filter_show" title=""  draggable scrollable  >
            <Row>
          <Form :inline="false" :model="filterForm"  ref="filterForm" :label-width="100">

            <template v-for="(item,index) in fields_data" >
                <Col span="12" v-if="item.filter">
                <FormItem :label="item.title"  :prop="item.slot" >
                    
                  <Select :transfer="true" v-model="filterForm[item.slot]" filterable  clearable  placeholder="" v-if=" item.data_type == 'select' ">
                    <Option v-for="(option_item,option_index) in item.options" :key="option_index" :value="option_index">{{ option_item }}</Option>
                  </Select>

                  <RadioGroup  v-else-if=" item.data_type == 'radio' "  v-model="filterForm[item.slot]" clearable>
                    <template v-for="(option_item,option_index) in item.options">
                      <Radio v-if=" /^\d+$/.test(option_index) " :label="parseInt(option_index)"><span>{{ option_item }}</span></Radio>
                      <Radio v-else-if=" /^\d+\.+\d+$/.test(option_index) " :label="parseFloat(option_index)"><span>{{ option_item }}</span></Radio>
                      <Radio v-else  :label="option_index"><span>{{ option_item }}</span></Radio>
                    </template>
                  </RadioGroup>

                  <CheckboxGroup  v-else-if=" item.data_type == 'checkbox' "  v-model="filterForm[item.slot]">
                    <template v-for="(option_item,option_index) in item.options">
                      <Checkbox  :label="option_index"><span>{{ option_item }}</span></Checkbox>
                    </template>
                  </CheckboxGroup>

                  <DatePicker
                    v-else-if=" item.data_type == 'date' "
                    v-model="filterForm[item.slot]"
                    type="daterange"
                    format="yyyy-MM-dd"
                    placeholder=""
                    :transfer="true"  clearable
                    @on-change="(datetime) =>{ changeDatetime(datetime,'filter',item.slot)}"
                  >
                  </DatePicker>

                  <DatePicker
                    v-else-if=" item.data_type == 'datetime' "
                    v-model="filterForm[item.slot]"
                    type="datetimerange"
                    format="yyyy-MM-dd HH:mm:ss"
                    placeholder=""
                    :transfer="true"  clearable
                    @on-change="(datetime) =>{ changeDatetime(datetime,'filter',item.slot)}"
                  >
                  </DatePicker>

                  <Input v-model="filterForm[item.slot]" clearable  :placeholder="'' + item.title" v-else></Input>
                    
                </FormItem>
                </Col>
            </template>


          </Form>
          </Row>
          <template slot="footer" class="dialog-footer">
            <Button  type="default" @click="restFilter"></Button>
            <Button type="primary"  @click="filter"></Button>
          </template>
        </Modal>


      <Modal
        :width="300"
        v-model="del_confirm_dlg"
        title=""
        @on-ok="delFormAction"
        @on-cancel="del_confirm_dlg = false">
        <p style="font-size: 16px"><Icon type="md-alert" style="color: #ff9900; font-size: 24px;" /> </p>
      </Modal>


    </div>

</template>

<style scoped>
  /*.ivu-modal-content{ height: 100% !important;}
  .ivu-modal-body{ height: calc(100% - 110px) !important;}
  .ivu-modal-body{ overflow: auto ;}
  .ivu-input-wrapper{ width: 98% !important;}*/
   .demo-spin-icon-load{
        animation: ani-demo-spin 1s linear infinite;
   }
   .ivu-form .ivu-col{ margin-bottom: 0 !important;}

   .error_tips{ color:#ed4014; text-align: left;}
</style>

<script>
    import Vue from 'vue'
    import axios from 'axios'
    import {jsonExport,jsonImport} from './jsonUtils'
    import expandRow from './table-expand.vue'
    import VcForm from './form.vue'
    import qs from 'qs'
    /*
    iviewCDN*/
    /*import iView from 'iview'
    import 'iview/dist/styles/iview.css';
    Vue.use(iView)*/

    export default {
        name:'vc-table',
        props:['formTabs','importServer','formLabelWidth','modelWidth','modelHeight' ,'expand','showToolbar','cellEvent','checkbox','server','page','limit','width','height','operateWidth','showEditBtn','showDelBtn','showAddBtn','uploadFileServer','uploadFileMaxSize','editorConfig','isReady'],//
        components:{VcForm},
        data() {
            return {
                del_current_row: {},
                del_confirm_dlg: false,
                cid: '', //ID

                //
                is_import_disabled: false, //
                import_file_name: '', //
                import_file_error: '', //
                importModal: false, //
                importExcelData: [], //
                importExcelPercentage:0, //
                importCurrentPage:0, //

                //
                currentRowData:{}, //
                oldCurrentRowData:{},
                dataForm:{}, //
                editDataForm:{}, //
                dataFormTitle:'',
                ruleValidate:{}, //
                token:'', //

                //
                filterForm: {},
                filter_show: false, //
                filter_form_width:0,
                keywords: '',
                keywords_field:[], //

                //
                showTotal:true, //
                showElevator:true, //
                showSizer:true, //page-size
                currentPage:1,
                pageSize:20,

                //
                exportData: [], //
                exportFileType: 'xlsx', //
                totalPages: 1, //
                downloadTips: false, //
                percentage:0, //
                downloadError:'', //
                loading:false,
                downloadCurrentPage:0,

                //
                tableData: [],
                fields_data:[], //
                checkList: [], //
                total: 0,
                orderField : '',
                orderSort: 'desc',
                columns : [], //
                selectRows : [], //
                currentSelectRow : {}, //

            }
        },
        mounted(){
            let that = this
            that.pageSize = that.limit
            that.loadTableField()
            that.refresh()
            that.resizeWin()
            window.onresize = () => {
                that.resizeWin()
            }

        },
        updated() {
            let that = this
            //
            if(that.$props.cellEvent != undefined && that.$props.cellEvent != '' && that.$props.cellEvent != null){
                let callfun = that.$props.cellEvent
                callfun(that.tableData)
            }
            
        },
        methods: {
            closeImportDlg: function(){
                this.importModal = false
                this.import_file_name = ''
                this.import_file_error = ''
                this.importExcelPercentage = 0
                this.importCurrentPage = 0
                this.is_import_disabled = false
            },
            validateInt: function(rule, value, callback){
                if (/^[0-9]+$/.test(value)) {
                    callback();
                } else {
                    return callback(new Error(""));
                }
            },
            validateSelect: function(rule, value, callback){
                console.log('rule',rule)
                if ((rule.field == 'categories_id' && value != '') || /^[0-9]+$/.test(value)) {
                    callback();
                } else {
                    return callback(new Error(""));
                }
            },
            validateFile: function(rule, value, callback){
                console.log('rule',rule)
                console.log('value=',value)
                if (value != '') {
                    callback();
                } else {
                    return callback(new Error(""));
                }
            },
            validateNumber: function(rule, value, callback){
                if (/^[0-9]+\.*[0-9]+$/.test(value)) {
                    callback();
                } else {
                    return callback(new Error(""));
                }
            },
            

            getUrlParam: function (url,paraName){
                let arrObj = url.split("?");

                if (arrObj.length > 1) {
                    let arrPara = arrObj[1].split("&");
                    let arr;

                    for (let i = 0; i < arrPara.length; i++) {
                        arr = arrPara[i].split("=");

                        if (arr != null && arr[0] == paraName) {
                            return arr[1];
                        }
                    }
                    return "";
                }
                else {
                    return "";
                }
            },
            //
            dateFormat(dateObj,fmt){
                let obj = {
                    "m+" : dateObj.getMonth() + 1,                 //
                    "d+" : dateObj.getDate() - 1,                    //
                    "H+" : dateObj.getHours(),                   //
                    "i+" : dateObj.getMinutes(),                 //
                    "s" : dateObj.getSeconds(),                 //
                };
                if(/(Y+)/.test(fmt)) fmt = fmt.replace(RegExp.$1, (dateObj.getFullYear()+""));
                for(let k in obj){
                    if(new RegExp("("+ k +")").test(fmt)){
                        fmt = fmt.replace(RegExp.$1, obj[k] < 10 ? "0" + obj[k] : obj[k]);
                    }
                }
                return fmt;
            },
            //
            triggerUpload(){
                this.import_file_name = ''
                this.import_file_error = ''
                this.importExcelPercentage = 0
                this.importCurrentPage = 0
                this.is_import_disabled = false
                this.$refs.importExcelForm.click()
            },
            //EXCEL
            importExcel(fileForm){
                if(fileForm.target.files[0].type != 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet' && fileForm.target.files[0].type != 'application/vnd.ms-excel'){
                    this.$Message.error('xlsx,xls');
                    this.is_import_disabled = true
                    return false
                }

                this.import_file_name = fileForm.target.files[0].name

                jsonImport(fileForm.target,this.callbackUploadExcelData)
            },
            //EXCEL
            callbackUploadExcelData(file_data){
                let that = this
                if(file_data.length == 0) return false

                let newData = []
            

                file_data.forEach(function (v,k) {
                    let item_data = {}
                
                    that.columns.forEach(function (val, key) {
                        if(val['title'] != undefined && val['slot'] != 'action'){
                            let new_val = v[val['title']]
                            if((val['data_type'] == 'datetime' || val['data_type'] == 'date') && new_val > 40000 && new_val < 90000){
                                new_val = that.dateFormat(new Date(1900, 0, new_val),'Y/m/d H:i:s')  //42747 
                            }

                            if(typeof val['options_name'] != "undefined" && val['options_name'] != ''){
                                let flag = false
                                for(let n in val['options_name']){
                                    if(val['options_name'][n] == new_val){
                                        flag = true
                                        new_val = parseInt(n.replace(/\'/g,''))
                                    }
                                }
                                if(flag == false){
                                    that.import_file_error += ' '+ (k+2) +'  '+new_val+'  '+val['title']+" <br>";
                                }

                            }else if(typeof val['options'] != "undefined" && val['options'] != ''){
                                let flag = false
                                for(let n in val['options']){
                                    if(val['options'][n] == new_val){
                                        flag = true
                                        new_val = parseInt(n.replace(/\'/g,''))
                                    }

                                }
                                if(flag == false){
                                    that.import_file_error += ' '+ (k+2) +'  '+new_val+'  '+val['title']+" <br>";
                                }
                            }

                            if(typeof new_val == "undefined")  new_val = ''

                            item_data[val['slot']] = new_val
                        }
                    })
                    newData[k] = item_data
                })

                if(that.import_file_error != ''){
                    that.is_import_disabled = true
                    that.$refs.importExcelForm.value = ''
                } 

                console.log(newData)

                this.importExcelData = newData
            },
            //
            startImportData(){
                let that = this

                //20
                let pageNum = 20
                let pages = Math.ceil(that.importExcelData.length / pageNum)

                if(that.importCurrentPage >= pages){
                    if(that.importExcelPercentage == 100){
                        that.$Message.success('')
                        that.refresh()
                    }
                    return false
                } 

                let post_data = that.importExcelData.slice(that.importCurrentPage * pageNum,(that.importCurrentPage + 1) * pageNum)

                if(post_data != '' && post_data != null && post_data.length != 0){
                    that.post(that.importServer,{data:JSON.stringify(post_data)}).then(function(data){
                        if(data.status == 200 && data.data.code == 0){
                            if(data.status == 200){
                                that.importExcelPercentage = Math.ceil((that.importCurrentPage + 1) / pages * 100)
                            }
                            that.importCurrentPage ++
                            that.startImportData()
                        }else if(data.data.code != 0){
                            that.import_file_error = data.data.msg + '<br>'
                            that.is_import_disabled = true
                        }
                        
                    })
                }

            },
            //
            downloadTemplate(){
                let tpl_data = []
                let item = []
                //
                console.log('this.columns',this.columns)
                this.columns.forEach(function (v,k) {
                    if(['action','create_time','update_time'].indexOf(v['slot']) == -1 && v['slot'] != undefined && v['data_type'] != 'img'){
                        //HTML
                        let label = v['title'].replace(/<[^>]*>/g,'')
                        item[label] = ''
                    }
                });
                tpl_data.push(item)
                jsonExport(tpl_data,'xlsx','')
            },
            //
            changeDatetime: function (datetime, formType ,prop) {
                if(formType == 'filter'){
                    this.filterForm[prop] = datetime
                    this.filter_show = true
                }
            },
            //
            addForm: function () {
                //this.$refs['addDataDlg'].$refs['addDataForm'].resetFields()
                let that = this
                console.log(this.fields_data)

                this.fields_data.forEach(function (v,k) {
                    if(v['data_type'] != 'select' && v['data_type'] != 'checkbox' && v['data_type'] != 'radio'){
                        that.$set(that.$refs['addDataDlg'].dataForm,v.slot,'')

                        if(v['data_type'] == 'image' || v['data_type'] == 'img' || v['data_type'] == 'file'){
                            that.$set(that.$refs['addDataDlg'].dataForm,v['slot'] + '_file_info',[])
                        }
                    }
                })

                this.dataFormTitle = ''
                if(this.$route.meta.filter_field != ''){
                    let form_cid = '\'' + this.cid + '\''
                    if(this.$route.meta.filter_field == 'model_id')   form_cid = this.cid
                    this.$refs['addDataDlg'].dataForm[this.$route.meta.filter_field] =  form_cid
                }

                //
                this.$refs['addDataDlg'].$refs.dlg.$refs.content.scrollTop = 0;
                this.$refs['addDataDlg'].$refs.dlg.$refs.content.scrollLeft = 0;

                this.$refs['addDataDlg'].dataFormShow = true

            },
            //
            editForm: function (index, row) {
                let that = this
                console.log('row=',row)
               // that.$refs['editDataDlg'].$refs['editDataForm'].resetFields()

                that.fields_data.forEach(function (v,k) {

                    if(v['data_type'] == 'image' || v['data_type'] == 'img' || v['data_type'] == 'file'){
                        let file = row[v['slot'] + '_file_info']
                        let file_list = []
                        if(file != ''){
                            file_list = file
                        }

                        that.$set(that.$refs['editDataDlg'].dataForm,v.slot,row[v['slot']])

                        //that.$refs['editDataDlg'].$refs[v['slot']][0].uploadList = file_list
                        that.$set(that.$refs['editDataDlg'].dataForm,v['slot'] + '_file_info',file_list)

                    }else if(v['data_type'] == 'date' && (row[v['slot']] == 0 || row[v['slot']] == '')){
                        that.$set(that.$refs['editDataDlg'].dataForm,v.slot, '')
                    }else if(v['slot'] == 'relation_field_id'){
                        //
                        that.$set(that.$refs['editDataDlg'].dataForm,'relation_field_id_options', row['relation_field_id_options'])
                        that.$set(that.$refs['editDataDlg'].dataForm,'relation_field_id', row['relation_field_id'])
                    }else{
                        let field_val = row[v['slot']]
                        if(v['relation_model_id'] > 0){
                            field_val = '\'' + field_val + '\''
                        }
                        that.$set(that.$refs['editDataDlg'].dataForm,v.slot,field_val)

                    }
                })

               // this.editDataForm = row
               that.$refs['editDataDlg'].$refs['editDataForm'].validate()

               that.dataFormTitle = ''
               that.$refs['editDataDlg'].dataFormShow = true

            },
            //
            delForm(row){
                this.del_current_row = row
                //this.$emit('on-del',row)
                this.del_confirm_dlg = true
            },
            delFormAction(){
                this.$emit('on-del',this.del_current_row)
            },
            //
            saveAddDataForm: function (data) {
                this.$emit('on-add',data)
            },
            //
            saveEditDataForm: function (data) {
                this.$emit('on-edit',data)
            },
            post: function (url,data) {
                let config = {
                    headers : {
                        'Content-Type':'application/x-www-form-urlencoded'
                    },
                };
                return axios.post(url, qs.stringify(data),config)
            },
            get: function (url) {
                return axios.get(url)
            },
            //
            formatter:function (row, field_name, data_type, options,options_name) {
                let cellValue = row[field_name]

                if((data_type == 'switch' || data_type == 'select' || data_type == 'radio') && options != '' && options != undefined){
                    if(typeof options_name != "undefined"){
                        cellValue = options_name[cellValue]
                    }else{
                        cellValue = options[cellValue]
                    }

                }else if(data_type == 'date' && cellValue == 0){
                    cellValue = ''
                }else if(data_type == 'image' || data_type == 'img'){
                    let img = ''
                    /*row[field_name + '_file_info'].forEach(function (v,k) {
                        img = img + '<img src="' + v.full_url + '" style="width:60px" /> '
                    })*/
                    if(typeof row[field_name + '_file_info'] != "undefined" && row[field_name + '_file_info'].length != 0){
                       img = '<img src="' + row[field_name + '_file_info'][0].full_url + '" style="width:60px" /> ' 
                    }
                    
                    cellValue = img
                }else if(data_type == 'url' || data_type == 'file'){
                    let file = ''
                    if(typeof row[field_name + '_file_info'] != "undefined"){
                        row[field_name + '_file_info'].forEach(function (v,k) {
                            file = file + '<a href="' + v.full_url + '" target="_blank">' + v.url + '</a> '
                        })
                    }else{
                        file = row[field_name]
                    }
                    
                    cellValue = file
                }

                if(typeof row[field_name + '-html'] != "undefined"){
                    cellValue = row[field_name + '-html']
                }

                return cellValue
            },
            //
            currentSelect:function (selection, row) {
                this.currentSelectRow = row
                this.$emit('on-select',selection, row)
            },
            //
            getSelectRows:function (selection) {
                this.selectRows = selection
                this.$emit('on-selection-change',selection)
            },
            //
            selectAllCancel:function (selection) {
                this.$emit('on-select-all-cancel',selection)
            },
            //
            selectAll:function (selection) {
                this.selectRows = selection
                this.$emit('on-select-all',selection)
            },
            cellFun: function (callfun,index,row) {
                //
                callfun(index,row)
            },

            pivot: function () {
                /*this.$exportExcel({
                    url: 'http://www.b2b.com/api/Table/index'
                })*/
            },

            //
            pullData: function (currentPage,callback,action) {
                let that = this
                let url = that.server

                if(typeof that.isReady == "function"){
                    that.isReady(that)
                }

                that.token = that.getUrlParam(url,'token')

                if(action != undefined && action == 'getField'){  //
                    that.post(url,{
                        action: action
                    }).then(function (data) {
                        callback(data.data)
                    })
                }else{  //
                    let post_url = url + '?'+ that.page + '=' + currentPage
                    if(url.indexOf('?') != -1){
                        post_url = url + '&'+ that.page + '=' + currentPage
                    }

                    that.post(post_url,{
                        pageSize: that.pageSize,
                        orderField: that.orderField,
                        orderSort: that.orderSort,
                        keywords: that.keywords,
                        keywords_field: that.keywords_field,
                        filter: that.filterForm,
                        //offset  limit 
                        offset: that.pageSize * (currentPage - 1),
                        limit: that.pageSize
                    }).then(function (data) {
                        callback(data.data)
                    })
                }
            },

            //
            restFilter: function () {
                this.$refs['filterForm'].resetFields()
            },
            //
            filter: function () {
                this.filter_show = false
                this.refresh()
            },
            //
            updateTableField: function (data) {
                let that = this
                that.columns = []
                that.fields_data = []
                //
                if(that.checkbox == true){
                    that.columns.push({
                        type: 'selection',
                        width: 50,
                        align: 'center',
                        fixed: true
                    })
                }


                //
                if(that.expand){
                    that.columns.push({
                        type: 'expand',
                        width: 50,
                        render: (h, params) => {
                            return h(expandRow, {
                                props: {
                                    row: params.row.expandData
                                }
                            })
                        }
                    })
                }

                let form_tabs = []

                //
                data.data.fields.forEach(function (val,key) {
                    form_tabs.push(val['prop'])
                    let filterName = val['prop']
                    if(val['filterName'] != '' &&  val['filterName'] != undefined){
                        filterName = val['filterName']
                    }

                    if(val['data_type'] == 'string'){
                        that.keywords_field.push(filterName)
                    }
                    if(val['filter'] == true){
                        //v-model  Vue.$set(obj, key, val)  v-model
                        that.$set(that.filterForm,filterName,'')
                        //that.filterForm[val['prop']] = ''  //v-model v-model
                    }

                    if(val['data_form'] == true){
                        let defalut_val = '';
                        if(val['default'] != undefined) defalut_val = val['default']
                        if(typeof defalut_val == "string") defalut_val = defalut_val.trim()
                        that.$set(that.dataForm,val['prop'],defalut_val)

                        if(val['validate'] != undefined && val['validate'] == true){
                           if(['tinyint','smallint','int','bigint'].indexOf(val['field_type']) != -1){
                                if(val['data_type'] == 'select'){
                                    that.ruleValidate[val['prop']] = [{required: true, validator: that.validateSelect, trigger: 'blur'}]
                                }else{
                                    that.ruleValidate[val['prop']] = [{required: true, validator: that.validateInt, trigger: 'blur'}]
                                }
                                
                           }else if(['float','decimal','double'].indexOf(val['field_type']) != -1){
                                that.ruleValidate[val['prop']] = [{required: true, validator: that.validateNumber, trigger: 'blur'}]
                           }else{
                               if(val['data_type'] == 'img' || val['data_type'] == 'file' ){
                                   //validateFile
                                   that.ruleValidate[val['prop']] = [{required: true, validator: that.validateFile, trigger: 'blur'}]
                               }else{
                                    that.ruleValidate[val['prop']] = [{required: true, message: val['label'] + '', trigger: 'blur'}]
                               }
                                
                           }

                        }

                    }

                    let col = {
                        title: val['label'],
                        //key: val['prop'],
                        slot: val['prop'],
                        fixed: val['fixed'],
                        sortable: val['sortable'],
                        renderHeader: (h,params) => {
                            if(params.column.tips){
                                return h('span',[
                                    h('Tooltip',{props:{placement:"bottom",maxWidth:240}},[
                                        h('Icon',{props:{type:"md-help-circle"}}),
                                        h('div',{slot:"content"},[
                                            h('div',{style:{fontWeight:300},domProps:{innerHTML:params.column.tips}})
                                        ])
                                    ]),
                                    h('span',{
                                            props:{}, //
                                            style:{ fontSize:"12px"}, //
                                            on:{ //
                                                /*'on-click':(e)=>{
                                                    console.log(e.target.value)
                                                }*/
                                            },
                                            domProps:{innerHTML:params.column.title}  //HTML
                                        }
                                    )
                                ])
                            }else{
                               // return h('span',{style:{ fontSize:"12px"}},params.column.title)
                                return h('span',{style:{ fontSize:"12px"},domProps:{innerHTML:params.column.title}})
                            }

                        },

                        data_type: val['data_type'],
                        is_tree: val['is_tree'],
                        width: val['width'],
                        tips: val['tooltip'],
                        options: val['options'],
                        options_name: val['options_name'],
                        filter: val['filter'],
                        field_length: val['length'],
                        data_form: val['data_form'],
                        extension: undefined != val['extension'] ? val['extension'] : '',
                        filter_field: val['filter_field'],
                        filter_form_name: val['filter_form_name'],
                        field_id: val['field_id'],
                        filter_url: val['filter_url'],
                        is_disabled: val['is_disabled'],
                        field_type: val['field_type']
          
                    }

                    if(that.checkbox == true){
                        col['_checked'] = val['checked'] == undefined ? false : val['checked']
                    }

                    col['_disabled'] = val['disabled'] == undefined ? false : val['disabled'];

                    that.fields_data.push(col)
                    if(val['show'] == true){
                        that.checkList.push(val['prop'])
                        that.columns.push(col)
                    }
                })

                if(typeof that.formTabs != "undefined" && that.formTabs.length == 0){
                    that.formTabs[0] = {}
                    that.formTabs[0]['tab_name'] = ''
                    that.formTabs[0]['tab_fields'] = form_tabs
                }

                //
                if((that.rowAction != undefined  && that.rowAction.length > 0) || that.showAddBtn || that.showEditBtn || that.showDelBtn){
                    that.columns.push({
                        title: '',
                        slot: 'action',
                        width: that.operateWidth,
                        align: 'center',
                        fixed:'right',
                        renderHeader:(h,params) => {
                            return h('span',{style:{ fontSize:"12px"}},params.column.title)
                        }
                    })
                }

                if(typeof that.isReady == "function"){
                    that.isReady(that)
                }

            },
            //
            loadTableField: function () {
                this.pullData(1,this.updateTableField,'getField')
            },
            //
            search: function () {
                this.refresh()
            },
            //
            getExportData: function (data) {
                if(data.data.data == undefined){
                    this.downloadError = '';
                    return false
                }

                let that = this
                data.data.data.forEach(function (val, index) {
                    let item = []
                    //
                    that.columns.forEach(function (v,k) {
                        if(v['slot'] != 'action' && typeof v['slot'] != "undefined"){
                            //HTML
                            let label = v['title'].replace(/<[^>]*>/g,'')
                            let value = val[v['slot']]
                            if(typeof v['options_name'] != "undefined" && v['options_name'] != ''){
                                value = v['options_name'][value]
                            }else if(typeof v['options'] != "undefined" && v['options'] != ''){
                                value = v['options'][value]
                            }
                            item[label] = value
                        }
                    });
                    that.exportData.push(item)
                })

                this.percentage = Math.ceil(this.downloadCurrentPage / this.totalPages * 100)

                //
                if(this.downloadCurrentPage == this.totalPages){
                    this.downloadTips = false
                    this.downloadError = ''
                    this.percentage = 0
                    this.downloadCurrentPage = 0
                    jsonExport(this.exportData,this.exportFileType)
                    this.exportData = []
                }else{
                    this.downloadExport(this.exportFileType)
                }

            },
            //
            downloadExport: function (type) {
                if(this.total == 0){
                    this.$Message.error('');
                    return false
                }
                this.downloadTips = true
                this.exportFileType = type
                this.downloadCurrentPage ++
                //
                this.pullData(this.downloadCurrentPage,this.getExportData)
            },
            //
            refresh: function (current_id) {
                this.loading = true
                this.pullData(this.currentPage,this.getList)

                //this.$refs['addDataDlg'].resetDataForm(true)

                //select
                let that = this
                that.fields_data.forEach(function (item,index) {
                    if(item.data_type == 'select'){
                        let sel_val = that.$refs['addDataDlg'].dataForm[item.slot]
                        if(typeof current_id != "undefined" && current_id != null){
                            sel_val = current_id
                        }
                        that.$refs['addDataDlg'].selectEvent(item,sel_val,false)
                    } else if(item.data_type == 'text'){
                        that.$refs['addDataDlg'].dataForm[item.slot] = ''
                    }
                })


                //
                that.$refs['addDataDlg'].$refs.dlg.$refs.content.scrollTop = 0;
                that.$refs['addDataDlg'].$refs.dlg.$refs.content.scrollLeft = 0;
                that.$refs['editDataDlg'].$refs.dlg.$refs.content.scrollTop = 0;
                that.$refs['editDataDlg'].$refs.dlg.$refs.content.scrollLeft = 0;

                that.$refs['addDataDlg'].dataFormShow = false;
                that.$refs['editDataDlg'].dataFormShow = false;

            },
            //
            resizeWin: function () {
                //page-countsizes 
                if(document.body.offsetWidth < 768){
                    this.showTotal = false
                    this.showElevator = false
                    this.showSizer = false
                    this.label_width = 120
                }else{
                    this.showTotal = true
                    this.showElevator = true
                    this.showSizer = true
                    this.label_width = 80
                }

                this.filter_form_width = document.body.offsetWidth * 0.8
            },
            //
            fieldChange(check_val){
                let that = this
                //
                that.fields_data.forEach(function (val,k) {
                    if(check_val.indexOf(val.slot) != -1){
                        console.log(val.slot)
                        let flag = true
                        //
                        that.columns.forEach(function (val2,k2) {
                            if(val.slot == val2.slot){
                                flag = false
                            }
                        })
                        if(flag == true)  that.columns.push(val)
                    }else{
                        //
                        that.columns.forEach(function (val2,k2) {
                            if(val.slot == val2.slot){
                                that.columns.splice(k2,1)
                            }
                        })
                    }
                })
            },
            //
            sort(column){
                if(column.key == undefined){
                    this.orderField = column.column.slot
                }else{
                    this.orderField = column.key
                }

                this.orderSort = column.order
                this.refresh()
            },
            //
            getList(data){
                let that = this
                if(data.data.data == undefined){
                    let msg = ''
                    if(data.data.message != undefined) msg = msg + data.data.message
                    if(data.data.msg != undefined) msg = msg + data.data.msg
                    this.$Message.error(msg);
                    return false
                }

                data.data.data.forEach(function (row,row_index) {
                    that.fields_data.forEach(function (item,index) {
                        if(item.data_type == 'select' && item.is_tree == true && typeof row[item.slot] == 'number'){
                            row[item.slot] = "'" + row[item.slot] + "'"
                        }
                    })
                })

                this.tableData = data.data.data
                this.total = parseInt(data.data.total)
                this.totalPages = Math.ceil(this.total / this.pageSize)
                this.loading = false



            },
            //
            handleSizeChange(val) {
                this.pageSize = val
                this.refresh()
            },
            //
            handleCurrentChange(val) {
                this.currentPage = val
                this.refresh()

            }
        }
    }
</script>

<style scoped="true" >
    .filter-form-content{ max-height:460px; display: block; overflow-y: auto;}
    .dropdown-content{ max-height: 260px; padding: 10px; overflow-y: auto; max-width: 500px; overflow-x: auto }
    .ivu-col{ margin-bottom: 10px;}
    .filter-form-content .ivu-col{ margin-bottom: 0;}
    .filter-form-content .ivu-form-item{ margin-bottom: 10px;}
    .table-tools .ivu-btn{ margin-left: -1px !important;  border-radius: 0px; height: 32px;}
    .table-tools .ivu-dropdown:last-child .ivu-btn{ border-top-right-radius: 4px !important; border-bottom-right-radius: 4px !important;  }
    .pagination{ margin-top: 10px; font-size: 12px;}
    .table-tools .ivu-btn-default:hover{ border-color: #dcdee2}

    .vertical-center-modal{
        display: flex;
        align-items: center;
        justify-content: center;
    }
    .vertical-center-modal .ivu-modal{
        top: 0;
    }

    .import-btn .ivu-col{
        text-align: center;
    }

</style>

